@import 'defaults';

.NotFound {
    display: flex;
    flex-direction: column;
    align-items: center;
    height: 100vh;
    
    p {
        margin: 0;
        font-size: 3.5rem;
        color: white;
    }

    img {
        margin-top: 5vh;
        width: 40vw;
    }
}

.title {
    margin: 0 0 5vh 0;
    text-shadow: 0px 2px 3px black, 0px 5px 6px black;
    -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
     -khtml-user-select: none; /* Konqueror HTML */
       -moz-user-select: none; /* Old versions of Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome, Edge, Opera and Firefox */
}

// Login and Reset Password
.Login, .ResetPassword {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100vw;
    height: 100vh;
    padding: 7vh 0 0 0;
    box-sizing: border-box;
    transition: 1s;

    .container {
        min-width: 20vw;
        min-height: 41vh;
        padding: 2.5vh 2vw 3vh 2vw;
        box-sizing: border-box;
        background-color: white;
        border-radius: 1rem;

        .input {
            margin-top: 2vh;
        }

        .google {
            display: flex;
            flex-direction: row;
            align-items: center;
            margin-bottom: 1vh;

            hr {
                margin: 0 auto 1.5vh auto;
                border-color: $dull-blue;
                background-color: $dull-blue;
                width: 25%;
            }
        }

        .googleLogin {
            display: flex;
            justify-content: center;
            margin: 0 auto 2vh auto !important;
            width: 40% !important;
            height: 6.5vh !important;
            border: 1px solid $dull-blue !important;
            border-radius: 5px !important;
            transition: 0.5s !important;

            img {
                height: 4vh;
                margin-left: 0.5vw;
            }

            p {
                font-weight: normal;
                font-size: 1rem;
                color: black !important;
                font-family: 'Source Sans' !important;
            }

            &:hover {
                background-color: #f2f2f2 !important;
            }
        }

        .MuiTextField-root {
            margin-bottom: 3.5vh;
            width: 100%;
            transition: 0.5s;
        
            &:hover {
                background-color: #f2f2f2;
            }

            &:active {
                background-color: none !important;
            }
        }

        .MuiFormLabel-root {
            color: black;
        }

        .MuiButton-root {
            height: 6vh;
            width: 100%;
            text-transform: none;
            font-weight: bold;

            .MuiButton-label {
                font-size: 1.15rem;
            }
        }

        .viewpkg {
            .MuiButton-label {
                font-size: 1.05rem;
            }
        }

        .MuiButton-text {
            margin: 1vh 0 0 0;
        }

        .MuiOutlinedInput-notchedOutline {
            border-color: black;
        }

        .halfContainer {
            display: flex;
            justify-content: space-between;
            width: 100%;

            .MuiTextField-root {
                width: 47%;
            }
        }

        .buttons {
            margin-top: 2vh;
            display: flex;
            flex-direction: row;
            justify-content: space-between;

            .forgot {
                .MuiButton-label {
                    justify-content: flex-start;
                }
            }

            .signup {
                .MuiButton-label {
                    justify-content: flex-end;
                }
            }

            .MuiButton-root {
                width: auto;
                height: 4vh;

                .MuiButton-label {
                    font-size: 1rem;
                }
            }
        }

        .errors p {
            display: flex;
            justify-content: center;
            margin: 0 0 0 0;
            font-family: 'Source Sans';
            font-weight: bold;
            color: $error;
        }

        .success {
            display: flex;
            justify-content: center;
            text-align: center;
            margin: 2vh 0 0 0;
            padding: 0 1vw 0 1vw;
            font-family: 'Source Sans';
            font-weight: bold;
            color: $success;
        }
    }
}

.Signup {
    .container {
        min-height: 57vh;
    }
}

.Forgot, .ResetPassword {
    .container {
        min-height: 30vh;

        .MuiButton-label {
            font-size: 1rem !important;
        }
    }
}

// Book
.Book {
    .container {
        min-width: 40vw;
        width: 40vw;
        padding-bottom: 4vh;

        .input {
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;

            .inputError {
                color: $error !important;
                border: 1px solid $error;

                &::placeholder {
                    color: $error !important;
                }

                &:focus {
                    outline: none !important;
                    border: 2px solid $error;
                }
            }
    
            .MuiTextField-root {
                width: 14vw;
                margin-bottom: 2.5vh;
            }

            .name {
                width: 9vw;
            }

            .email {
                width: 17vw;
            }

            .phone {
                width: 12vw;
            }

            .date {
                width: 14vw;
            }

            .location {
                width: 17vw;

                .MuiTextField-root {
                    width: 17vw;
                }
            }

            .dropdown {
                width: 9vw;
            }

            .notes {
                width: 100%;
            }
        }

        .BookButtons {
            display: flex;
            justify-content: flex-start;
            flex-direction: row-reverse;
        }

        .MuiButton-root {
            width: fit-content;
        }

        .signup {
            width: fit-content;
            margin: 0 5%;
            a {
                font-size: 1rem;
            }
        }
    }
}

.MuiPickersCalendarHeader-switchHeader p {
    color: $dull-blue;
}

.MuiPickersCalendarHeader-transitionContainer {
    height: 1.5rem !important;
}

// Dashboard
.Dashboard {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100vw;
    height: 100vh;

    ::-webkit-scrollbar {
        width: 16px;
    }
    ::-webkit-scrollbar-track {
        background: white;
    }
    ::-webkit-scrollbar-thumb {
        background: $steel-blue; 
    }

    .tabs {
        display: flex;
        flex-direction: row;
        align-items: center;
        width: 80vw;
        border-top-left-radius: 1vh;
        border-top-right-radius: 1vh;
        background-color: rgb(240, 240, 240);

        .leftmost {
            border-top-left-radius: 1vh;
        }

        .rightmost {
            border-top-right-radius: 1vh;
        }

        .tab {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 25%;
            height: 12vh;
            color: $dull-yellow;
            font-family: 'Josefin';
            font-weight: normal;
            font-size: 1.5rem;
            transition: 0.5s;
            z-index: 1;

            &:hover {
                color: $hover-yellow;
                text-shadow: 0px 7px 3px rgb(210, 210, 210), 0px 10px 6px rgb(210, 210, 210);
            }
        }

        .selected {
            background-color: $dull-yellow;
            color:rgb(240, 240, 240);

            &:hover {
                color: rgb(210, 210, 210);
                text-shadow: 0px 7px 3px $hover-yellow, 0px 10px 6px $hover-yellow;
            }
        }
    }

    .tabpanel {
        width: 80vw;
        height: 80vh;
        border-bottom-left-radius: 1vh;
        border-bottom-right-radius: 1vh;
        background-color: rgb(240, 240, 240);
    }
}

.emptyPanel {
    display: flex;
    align-items: center;
    flex-direction: column;

    img {
        margin-top: 5vh;
        height: 35vh;
    }

    h2 {
        margin-top: 5vh;
        text-align: center;
        color: $steel-blue;
        font-family: 'Josefin';
    }
}

.Booked {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-evenly;
    overflow-y: scroll;
    width: 80vw;
    height: 75vh;
    padding: 0 0 10vh 0;
    box-sizing: border-box;

    .container {
        width: 74vw;
        margin: 3.33vw 0 0 6.66vw;

        .newBooking {
            font-size: 1.1rem;
            background-color: $steel-blue;
            color: white;
            font-family: 'Josefin';
            text-transform: none;
    
            &:hover {
                background-color: $hover-steel;
            }
        }
    }

    .indivBooking {
        display: flex;
        flex-direction: column;
        width: 30vw;
        min-height: 45vh;
        margin-top: 3.33vw;
        border-radius: 1vw;
        background-color: $steel-blue;
        padding: 2vw;
        box-sizing: border-box;

        p {
            line-height: 1.5rem;
            margin: 0 0 1vh 0;
            font-family: 'Source Sans';
            font-weight: bold;

            span {
                color: $dull-yellow;
            }
        }

        .icons {
            align-self: flex-end;
            svg {
                margin-left: 1vw;
                cursor: pointer;
                transition: 0.5s;

                &:hover {
                    color:white;
                }
            }
            
            color:rgb(210, 210, 210);
        }

        a {
            margin-top: 3vh;

            img {
                border-radius: 1vw;
                width: 100%;
            }
        }
    }
}

.MuiSnackbar-root .MuiPaper-root {
    color: rgb(240, 240, 240);
    background-color: $steel-blue;
    display: flex;
    justify-content: center;
    font-weight: bold;
