    }

    .MuiPaper-root {
        min-width: 40vw;
        width: 40vw;
        padding: 2vh 2vw 4vh 2vw;
        border-radius: 1vw;
        box-sizing: border-box;

        .MuiDialogTitle-root, .MuiTypography-h6 {
            text-align: center;
            color: $steel-blue;
            margin-bottom: 1vh;
            font-size: 1.5rem !important;
            font-weight: bold;
        }

        .input {
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;

            .inputError {
                color: $error !important;
                border: 1px solid $error;

                &::placeholder {
                    color: $error !important;
                }

                &:focus {
                    outline: none !important;
                    border: 2px solid $error;
                }
            }
    
            .MuiTextField-root {
                width: 14vw;
                margin-bottom: 2vh;
            }

            .name {
                width: 9vw;
            }

            .email {
                width: 17vw;
            }

            .phone {
                width: 12vw;
            }

            .date {
                width: 14vw;
            }

            .location {
                width: 17vw;

                .MuiTextField-root {
                    width: 17vw;
                }
            }

            .dropdown {
                width: 9vw;
            }

            .notes {
                width: 100%;
            }
        }
    }

    .MuiButtonBase-root {
        font-weight: bold;
    }
