.Favorites {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-evenly;
    overflow-y: scroll;
    width: 80vw;
    height: 75vh;
    padding: 0 0 10vh 0;
    box-sizing: border-box;

    .container {
        width: 74vw;
        margin: 3.33vw 0 0 6.66vw;

        .toPortfolio {
            font-size: 1.1rem;
            background-color: $steel-blue;
            color: white;
            font-family: 'Josefin';
            text-transform: none;
    
            &:hover {
                background-color: $hover-steel;
            }
        }
    }

    .image {
        position: relative;
        border-radius: 8px;
        width: 100%;
        height: fit-content;

        .favoritesImage {
            border-radius: 8px;
            width: 100%;
            height: 100%;
        }
        
        .favoriteIcon {
            position: absolute;
            top: 1.5vh;
            right: 1.5vh;
            z-index: 1;
            cursor: pointer;
            background-color: white;
            padding: 10px;
            border: solid 4px transparent;
        }

        .cartIcon {
            top: calc(3vh + 47px);
        }

        .addToCart {
            color: green;
        }

        .captionOverlay {
            position: absolute;
            top: 0;
            display: flex;
            justify-content: center;
            align-items: center;
            border-radius: 8px;
            width: 100%;
            height: 100%;
            padding: 0 7.5%;
            box-sizing: border-box;
            font-size: 1.5rem;
            background-color: rgba(0, 0, 0, 0.5);
            font-family: 'Josefin';
            text-transform: uppercase;
            text-align: center;
            opacity: 0;
            transition: 0.5s;
            transition-delay: 0.25s;
            cursor: pointer;

            &:hover {
                opacity: 1;
                transition-delay: 0s;
            }
        }
    }

    .favoritesGrid {
        margin: 3vw 4vw 0 4vw;
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        column-gap: 4vw;
        row-gap: 4vw;
    }

    .portrait {
        grid-row: span 2;
        align-self: center;
    }
}

.Cart {
    .goButtons {
        display: flex;
        justify-content: space-between;
        width: 100%;
        margin: 3.33vw 6.66vw 0 6.66vw;

        .container {
            margin: 0;
            width: auto;
            height: auto;
        }
    }

    .itemContainer {
        position: relative;
        border-radius: 8px;
        width: 100%;
        height: fit-content;
        
        .image {
            margin: 0;
            width: 100%;
            height: fit-content;

            .cartImage {
                border-radius: 8px;
                width: 100%;
                height: 100%;
            }
        }

        .priceandquantity {
            display: flex;
            flex-direction: column;
            align-items: center;
            margin-top: 1.5vh;
            width: 100%;

            p {
                color: $steel-blue;
                margin: 0.75vh 0;
                font-size: 1.3rem;
            }

            .MuiIconButton-root {
                position: relative;
                top: -3px;
                padding: 3px;
            }
        }
    }

    .cartGrid {
        margin: 3vw 4vw 0 4vw;
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        column-gap: 4vw;
        row-gap: 4vw;
    }

    .portrait {
        grid-row: span 2;
        align-self: center;
    }
}

.receipt {
    .MuiPaper-root {
        display: flex;
        justify-content: center;
        width: 40vw;
        padding: 2vh 2vw 4vh 2vw;
        border-radius: 1vh;

        .MuiDialogTitle-root, .MuiTypography-h6 {
            text-align: center;
            color: $steel-blue;
            margin-bottom: 1vh;
            font-size: 1.5rem !important;
            font-weight: bold;
        }
    
        table {
            margin-bottom: 4vh;
        }

        th {
            padding-bottom: 2vh;
        }

        td, th {
            text-align: left;
            font-family: 'Source Sans';
            color: $steel-blue;
            font-weight: bold;
            vertical-align: center;
        }

        .total {
            td {
                padding-top: 2vh;
            }

            .totalPrice {
                padding-top: 0vh;
            }
        }

        .receiptItem {
            .receiptImage {
                margin: 0 auto;
                position: relative;
                right: 20px;
                height: 5vh;
                border-radius: 8px;
            }
        }
    }

    .MuiButtonBase-root {
        font-weight: bold;
    }
}

.User {
    display: flex;
    align-items: center;
    flex-direction: column;

    width: 80vw;

    .info {
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        height: 40vh;

        .basic {
            width: 40%;
            height: 80%;
            display: flex;
            flex-direction: column;
            align-items: flex-end;
            justify-content: space-evenly;
        }

        hr {
            margin: 0 5vw;
            height: 85%;
            border: 1px solid $dull-blue;
        }

        .confidential {
            width: 40%;
            height: 80%;
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            justify-content: space-evenly;
        }
    }

    .googleOnly {
        margin-top: 5vh;
        height: 30vh;

        hr {
            height: 75%;
            margin: 0 4vw;
        }
    }

    .MuiButton-root {
        font-weight: bold;
        min-width: 15vw;
        font-size: 1.2rem;
        margin-top: 4vh;
    }

    .MuiTextField-root {
        transition: 0.5s;

        &:hover {
            background-color: rgb(230, 230, 230);
        }
    }

    .errors p {
        margin-top: 3vh;
        color: $error;
        font-family: 'Source Sans';
        font-weight: bold;
    }

    .success {
        text-align: center;
        margin: 3vh 10vw 0 10vw;
        color: $success;
        font-family: 'Source Sans';
        font-weight: bold;
    }
}

.socials {
    display: flex;
    justify-content: space-around;
    margin-bottom: 4vh;

    img {
        width: 2vw;
        border-radius: 0 !important;
    }
}

.About, .Contact {
    .socials {
        width: 15vw;
    }
}

.Contact {
    .container {
        .input {
            .name {
                width: 11vw;
            }
            
            .subject {
                width: 23vw;
            }

            .email {
                width: 12vw;
            }
        }
    }
}

.Popup {
    .MuiPaper-root {
        width: 20vw;

        img {
            width: 15vw;
            align-self: center;
        }

        .MuiDialogTitle-root {
            h2 {
                font-family: 'Josefin';
                font-size: 1.7rem !important;
                text-transform: uppercase;
            }

            padding-top: 0;
            margin-top: 0;
        }

        p {
            margin: 0 0 1.5vh 0;
            font-size: 1.2rem;
            color: $steel-blue;
            font-family: 'Source Sans';
            font-weight: bold;
        }
    }
}

.Shop {
    width: 100vw;
    height: 100vh;

    .scrollToTop {
        color: white;
        position: absolute;
        bottom: 3vh;
        right: 3vw;
        cursor: pointer;
    }
    
    .animate__fadeOut {
        cursor: default;
    }

    .toCart {
        position: absolute;
        top: 7vh;
        right: 18vh;
        color: white;
        font-size: 1.3rem;
        letter-spacing: 1px;
        text-transform: none;
        font-family: 'Josefin';
        font-weight: bold;
        z-index: 2;
    }

    .main {
        width: 100vw;
        height: 100vh;
        overflow-y: scroll;
        display: flex;
        align-items: center;
        flex-direction: column;
        background-color: $dull-blue;
    }

    h1 {
        margin: 7vh 0 5vh 0;
    }

    .search {
        display: flex;
        flex-direction: column;
        align-items: center;
        height: fit-content;
        background-color: white;
        border-radius: 8px;

        .MuiTextField-root {
            margin: 1vw 1vw;
            width: 40vw;
            height: fit-content;
            transition: 0.5s;

            .MuiFormLabel-root {
                color: $dull-blue;
            }

            fieldset {
                border-color: $dull-blue;
            }

            &:hover {
                background-color: rgb(240, 240, 240);
            }
        }

        .filters {
            display: flex;
            flex-direction: row;
            justify-content: center;
            width: 40vw;

            .MuiFormControlLabel-root {
                width: fit-content;
            }
        }
    }

    .formattedImages {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        column-gap: 4.6666vw;
        row-gap: 11vh;
        margin: 10vh 0;
        width: 80vw;

        .shopLazyLoad {
            position: relative;
            border-radius: 16px;
            width: 23.555555vw;
            height: 22vw;
            justify-self: stretch;
            align-self: stretch;

            img {
                border-radius: 16px;
                width: 100%;
                height: 100%;
                object-fit: cover;
            }

            .priceOverlay {
                position: absolute;
                top: 0;
                display: flex;
                justify-content: center;
                align-items: center;
                border-radius: 16px;
                width: 100%;
                height: 100%;
                font-size: 1.5rem;
                background-color: rgba(0, 0, 0, 0.5);
                font-family: 'Josefin';
                text-transform: uppercase;
                text-align: center;
                opacity: 0;
                transition: 0.5s;
                transition-delay: 0.25s;
                cursor: pointer;

                &:hover {
                    opacity: 1;
                    transition-delay: 0s;
                }
            }

            .titleAdd {
                display: flex;
                flex-direction: row;
                justify-content: space-between;
                
                p {
                    font-family: 'Source Sans';
                    font-weight: bold;
                    font-size: 1.2rem;
                }

                .MuiIconButton-root {
                    color: white;
                    transform: scaleX(-1);
                }
            }
        }

        .portrait {
            grid-row: span 2;
            height: 48.6666vw;
            width: 23.555555vw;
        }
    }
}

.shopPopUp {
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    height: 100vh;
    transition: 0.5s;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 999;

    .popContainer {
        display: flex;
        flex-direction: column;
        align-items: center;
        background: white;
        padding: 4vh 2vw 3vh 2vw;
        border-radius: 12px;

        .MuiDialogTitle-root {
            padding-bottom: 0;
            padding-top: 0;
        }
        
        .MuiTypography-root {
            font-size: 1.8rem;
            font-family: 'Josefin';
            color: $dull-blue;
            text-align: center;
        }

        p {
            margin: 3vh 0 0 0;
            color: $dull-blue;
            font-family: 'Source Sans';
            font-weight: bold;
            font-size: 1.2rem;
        }

        img {
            height: 15vw;
            pointer-events: none;
            user-select: none;
        }

        .buttons {
            margin-top: 4vh;

            .MuiButton-label {
                font-weight: bold;   
            }
        }
    }
}


.addToCart {
    .container {
        padding: 3vh 2vw;

        p {
            margin: 2vh auto 0 auto;
            width: fit-content;

            .MuiButtonBase-root {
                color: $steel-blue;
            }
        }

        .MuiDialogActions-root {
            margin-top: 2vh;
            display: flex;
            justify-content: space-around;
        }
    }
}

.About {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100vw;
    overflow-y: scroll;
    height: 100vh;
    box-sizing: border-box;
    padding: 8vh 7vw 10vh 6vw;
    background-color: $dull-blue;
    position: relative;

    .main {
        display: flex;
        flex-direction: column;
    }

    .columns {
        display: flex;
        width: 88vw;
        justify-content: space-between;
    }
    
    .column {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    img {
        border-radius: 12px;
    }

    .fullImage {
        width: 26vw;
        height: 31.2vw;
        margin: 0 0 10vh 0;
    }

    .halfImage {
        width: 26vw;
        height: 17.334vw;
        margin: 10vh 0 0 0;
    }

    .fullPaper {
        width: 26vw;
        height: fit-content;
    }

    .halfPaper {
        width: 26vw;
        height: fit-content;
        margin: 0 0 10vh 0;
    }

    .widePaper {
        margin: 0 auto;
        width: 77vw;
        height: 60vh;
        display: flex;
        flex-direction: column;
        align-items: center;

        h1 {
            margin: 0;
            font-family: 'Source Sans';
            font-weight: bold;
            color: $dull-yellow;
        }

        .items {
            margin-top: 3vh;
            display: flex;
            justify-content: space-evenly;
            width: 70vw;

            .item {
                display: flex;
                flex-direction: column;
                align-items: center;
                height: 15vw;

                img {
                    height: 15vw;
                }

                h3 {
                    margin: 4vh auto 0 auto;
                    text-transform: none;
                    color: $steel-blue;
                    text-align: center;
                }
            }
        }
    }

    .MuiPaper-root {
        border-radius: 12px;
        padding: 4vh 2vw;
        box-sizing: border-box;

        h3 {
            margin: 0;
            color: $dull-yellow;
            font-size: 1.5rem;
            font-family: 'Source Sans';
            text-transform: uppercase;
        }

        p {
            color: $steel-blue;
            font-family: 'Source Sans';
            text-align: justify;
            font-weight: bold;
            font-size: 1rem;
        }
    }

    .scroll {
        position: absolute;
        right: 60px;
        bottom: 3vh;
        width: 35px;
        height: 50px;
        margin-left: -20px;
        margin-top: -35px;
        box-shadow: inset 0 0 0 3px #fff;
        border-radius: 25px;
        cursor: pointer;
        z-index: 2;

        &:before {
            position: absolute;
            right: 14px;
            content: '';
            width: 8px;
            height: 8px;
            background: #fff;
            margin-left: -4px;
            top: 8px;
            border-radius: 4px;
            -webkit-animation-duration: 2s;
                    animation-duration: 2s;
            -webkit-animation-iteration-count: infinite;
                    animation-iteration-count: infinite;
            -webkit-animation-name: scroll;
                    animation-name: scroll;
        }
    }

    @keyframes scroll {
        0% {
          opacity: 0;
          transform: translateY(0px);
        }
        100% {
          opacity: 1;
          transform: translateY(26px);
        }
    }
    @-webkit-keyframes scroll {
        0% {
          opacity: 0;
          transform: translateY(26px);
        }
        100% {
          opacity: 1;
          transform: translateY(0px);
        }
    }
}

.Packages {
