@import 'defaults';

// Preloader
.sk-cube-grid {
    width: 40px;
    height: 40px;
    margin: 100px auto;
}

.sk-cube-grid .sk-cube {
    width: 33%;
    height: 33%;
    background-color: white;
    float: left;
    -webkit-animation: sk-cubeGridScaleDelay 1.3s infinite ease-in-out;
    animation: sk-cubeGridScaleDelay 1.3s infinite ease-in-out;
}
.sk-cube-grid .sk-cube1 {
    -webkit-animation-delay: 0.2s;
    animation-delay: 0.2s;
}
.sk-cube-grid .sk-cube2 {
    -webkit-animation-delay: 0.3s;
    animation-delay: 0.3s;
}
.sk-cube-grid .sk-cube3 {
    -webkit-animation-delay: 0.4s;
    animation-delay: 0.4s;
}
.sk-cube-grid .sk-cube4 {
    -webkit-animation-delay: 0.1s;
    animation-delay: 0.1s;
}
.sk-cube-grid .sk-cube5 {
    -webkit-animation-delay: 0.2s;
    animation-delay: 0.2s;
}
.sk-cube-grid .sk-cube6 {
    -webkit-animation-delay: 0.3s;
    animation-delay: 0.3s;
}
.sk-cube-grid .sk-cube7 {
    -webkit-animation-delay: 0s;
    animation-delay: 0s;
}
.sk-cube-grid .sk-cube8 {
    -webkit-animation-delay: 0.1s;
    animation-delay: 0.1s;
}
.sk-cube-grid .sk-cube9 {
    -webkit-animation-delay: 0.2s;
    animation-delay: 0.2s;
}

@-webkit-keyframes sk-cubeGridScaleDelay {
    0%,
    70%,
    100% {
        -webkit-transform: scale3D(1, 1, 1);
        transform: scale3D(1, 1, 1);
    }
    35% {
        -webkit-transform: scale3D(0, 0, 1);
        transform: scale3D(0, 0, 1);
    }
}

@keyframes sk-cubeGridScaleDelay {
    0%,
    70%,
    100% {
        -webkit-transform: scale3D(1, 1, 1);
        transform: scale3D(1, 1, 1);
    }
    35% {
        -webkit-transform: scale3D(0, 0, 1);
        transform: scale3D(0, 0, 1);
    }
}

// Loader
.loader {
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100vw;
    height: 100vh;
    background-color: $dull-yellow;
    z-index: 999;
    animation-duration: 0.375s !important;

    img {
        height: 40vh;
    }
}

.loaderDashboard {
    top: 15.7vh;
    height: 80.3vh;
    width: 80vw;
    left: 10vw;
    border-bottom-left-radius: 8px;
    border-bottom-right-radius: 8px;
}

//$nav-width used here
.shiftedDashboard {
    left: calc(-6.2vw) !important;
}

html {
    font-size: 18px;
    overflow: hidden;
}

p, h1 {
    color: white;
    font-family: 'Josefin';
}

h1 {
    font-size: 3rem;
}

::-webkit-scrollbar {
    width: 16px;
}
::-webkit-scrollbar-track {
    background: $dull-yellow; 
}
::-webkit-scrollbar-thumb {
    background: white;
}

body {
    margin: 0;
    background-color: $dull-yellow;
}

img {
    display: block;
}

 a {
     text-decoration: none;
     color: inherit;
 }

.hidden {
    opacity: 0;
    z-index: -1 !important;
}

.visible {
    visibility: visible;
    opacity: 1;
    z-index: 2;
}

.gone {
    visibility: hidden;
    transition: visibility 0s linear 0.375s;
}

.Mui-error {
    color: $error !important;
}

.no-pointer-events {
    a, h1 {
        pointer-events: none;
        -webkit-touch-callout: none; /* iOS Safari */
        -webkit-user-select: none; /* Safari */
         -khtml-user-select: none; /* Konqueror HTML */
           -moz-user-select: none; /* Old versions of Firefox */
            -ms-user-select: none; /* Internet Explorer/Edge */
                user-select: none; /* Non-prefixed version, currently
                                      supported by Chrome, Edge, Opera and Firefox */
    }

    z-index: -1 !important;

    &:hover {
        z-index: -1 !important;
    }
}

.MuiButton-containedSecondary:hover {
    background-color: $hover-yellow !important;
}

// App
.App {
    width: 100vw;
    height: 100vh;
    transition: opacity 1s;
    background-color: $dull-blue;
}

// Nav 
$nav-width: 18vw;
.nav-button {
    position: fixed;
    top: 0;
    right: 0;
    padding: 0.3vw;
    height: 11vh;
    width: 10vh;
    border-bottom-left-radius: 20%;
    background-color: $dull-yellow;
    z-index: 2;

    &:hover {
        .line {
            stroke:white !important;
            box-shadow: 0px 7px 3px $hover-yellow, 0px 10px 6px $hover-yellow;
            
        }
    }

    .svg-container {
        display: inline-block;
        position: relative;
        width: 100%;
        padding-bottom: 100%;
        vertical-align: middle;
        overflow: hidden; 
    }

    .ham {
        display: inline-block;
        top: 0;
        right: 0;
        position: relative;
        cursor: pointer;
        -webkit-tap-highlight-color: transparent;
        transition: transform 400ms;
        -moz-user-select: none;
        -webkit-user-select: none;
        -ms-user-select: none;
        user-select: none;
    }
    .hamRotate.active {
        transform: rotate(45deg);
    }
    .hamRotate180.active {
        transform: rotate(180deg);
    }
    .line {
        fill: none;
        transition: stroke-dasharray 400ms, stroke-dashoffset 400ms;
        stroke: rgb(220, 220, 220);
        stroke-width: 5.5;
        stroke-linecap: round;
        transition: 0.5s !important;
    }
    .ham8 .top {
        stroke-dasharray: 40 160;
    }
    .ham8 .middle {
        stroke-dasharray: 40 142;
        transform-origin: 50%;
        transition: transform 400ms;
    }
    .ham8 .bottom {
        stroke-dasharray: 40 85;
        transform-origin: 50%;
        transition: transform 400ms, stroke-dashoffset 400ms;
    }
    .ham8.active .top {
        stroke-dashoffset: -64px;
    }
    .ham8.active .middle {
        //stroke-dashoffset: -20px;
        transform: rotate(90deg);
    }
    .ham8.active .bottom {
        stroke-dashoffset: -64px;
    }
}

.nav-bottom {
    position: fixed;
    top: 11.6vh;
    right: 0;
    width: 0;
    height: 0;
    border-top: 10vh solid $dull-yellow;
    border-left: 10.5vh solid transparent;
}

nav {
    position: fixed;
    right: 0;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-end;
    z-index: 2;
    background-color: $dull-yellow;
    width: $nav-width;
    height: 100vh;
    color: rgb(240, 240, 240);

    .animate__slideOutRightNav {
        -webkit-animation-delay: 0.1s;
        animation-delay: 0.1s;
    }

    ul {
        margin: 14vh 1.6vw 0 0;
        list-style-type: none;
        padding: 0;

        li {
            text-align: right;
            margin: 0 0 4.5vh 0;
            transition: 0.25s;

            a {
                font-size: 1.9rem;
                font-family: "Josefin";
                text-decoration: none;
                color: inherit;
            }

            &:hover {
                color: white;
                text-shadow: 0px 7px 3px $hover-yellow, 0px 10px 6px $hover-yellow;
            }
        }
    }

    .socials {
        display: flex;
        justify-content: flex-end;

        img {
            margin-right: 1.6vw;
            height: 2rem;
            width: 2rem;
        }
    }

    p {
        text-align: right;
        font-size: 1.15rem;
        margin: 2vh 1.6vw 3vh 0;
    }
}

// Landing Page
.Landing {
    width: 100vw;
    height: 100vh;
    overflow: hidden;

    .landing-image {
        position: absolute;
        top: 0;
        left: 0;
        object-fit: cover;
        width: 100vw;
        max-height: 100vh;
        pointer-events: none;
        animation-duration: 1s;
        animation-timing-function: ease-in-out;
        z-index: 1;
        border-bottom: 2vh solid white;
        box-sizing: content-box;
    }

    .panel {
        position: relative;
        top: -2vh;
        border-top: 2vh solid white;
        box-sizing: content-box;
        width: 100vw;
        height: 100vh;
    }

    .select {
        position: absolute;
        width: 50vw;
        height: 50vh;
        display: flex;
        justify-content: center;
        align-items: center;
        transition: 0.5s;
        transition: 0.5s;
        background-color: $dull-blue;

        &:hover {
            background-color: $dull-yellow;
        }

        h1 {
            text-align: center;
            font-size: 3.5rem;
            letter-spacing: 0.2rem;
        }
    }

    .center {
        top: 35vh;
        left: 35vw;
        width: 30vw;
        height: 30vh;
        background-color: white;
        border-radius: 8px;
        z-index: 3;

        h1 {
            color: $dull-blue;
        }

        &:hover , &:active {
            z-index: 1;
        }
    }

    .tl {
        top: 0vh;
        left: 0vw;
        border-bottom-right-radius: 8px;
    }

    .tr {
        top: 0vh;
        right: 0vw;
        border-bottom-left-radius: 8px;
    }

    .bl {
        bottom: 0vh;
        left: 0vw;
        border-top-right-radius: 8px;
    }

    .br {
        bottom: 0vh;
        right: 0vw;
        border-top-left-radius: 8px;
    }

    .waitForOther {
        animation-delay: 0.5s !important;
    }

    .scroll {
        position: absolute;
        left: 50%;
        bottom: 3vh;
        width: 40px;
        height: 70px;
        margin-left: -20px;
        margin-top: -35px;
        box-shadow: inset 0 0 0 3px #fff;
        border-radius: 25px;
        cursor: pointer;
        z-index: 2;

        &:before {
            position: absolute;
            left: 50%;
            content: '';
            width: 8px;
            height: 8px;
            background: #fff;
            margin-left: -4px;
            top: 8px;
            border-radius: 4px;
            -webkit-animation-duration: 2s;
                    animation-duration: 2s;
            -webkit-animation-iteration-count: infinite;
                    animation-iteration-count: infinite;
            -webkit-animation-name: scroll;
                    animation-name: scroll;
        }
    }

    @keyframes scroll {
        0% {
          opacity: 0;
          transform: translateY(46px);
        }
        100% {
          opacity: 1;
          transform: translateY(0px);
        }
    }
    @-webkit-keyframes scroll {
        0% {
          opacity: 0;
          transform: translateY(46px);
        }
        100% {
          opacity: 1;
          transform: translateY(0px);
        }
    }

    .portfolio {
        width: 100vw;
        height: 100vh;
        display: flex;
        align-items: center;
        justify-content: center;

        .portfolioTitle {
            display: flex;
            justify-content: center;
            height: 35vh;
            margin: 0;

            img {
                height: 35vh;
            }
        }

        .filters {
            position: relative;
            top: 4vh;
            background-color: white;
            display: flex;
            flex-direction: column;
            padding: 0 2vw 2vw 2vw;
            border-radius: 8px;
            height: fit-content;
            width: 15vw;
            box-sizing: border-box;

            p {
                color: $dull-blue;
                align-self: center;
                font-family: 'Source Sans';
                font-weight: bold;
                font-size: 1.5rem;
            }
        }

        .images {
            margin: 0 5vw;
            width: 62vw;
            height: 70vh;

            .slick-arrow, .slick-arrow::before {
                height: 5vw;
                width: 5vw;
                font-size: 3rem;
                transition: 0.5s;
                z-index: 1;
            }
        }

        .slick-dots {
            bottom: -8vh;
            left: -10%;
            width: 115%;
        }

        .slick-dots li button::before {
            height: 3vw;
            width: 3vw;
            font-size: 1.2rem;
            transition: 0.5s;
            color: white;
        }

        .portrait, .landscape {
            display: block !important;
            margin: 0 auto !important;
            width: auto !important;
            max-height: 70vh;
            border-radius: 16px;
        }

        .image {
            display: block !important;
            position: relative;
            width: fit-content !important;
            margin: 0 auto;

            &:focus {
                outline: none;
            }
        }

        .favoriteIcon {
            position: absolute;
            top: 1.5vh;
            right: 1.5vh;
            z-index: 1;
            cursor: pointer;
            background-color: white;
            padding: 10px;
            border: solid 4px transparent;
        }

        .notFavoritedIcon {
            border: 4px solid white !important;
        }

        .captionOverlay {
            position: absolute;
            top: 0;
            display: flex;
            justify-content: center;
            align-items: center;
            border-radius: 16px;
            width: 100%;
            height: 100%;
            padding: 0 7.5%;
            box-sizing: border-box;
            font-size: 1.5rem;
            background-color: rgba(0, 0, 0, 0.5);
            font-family: 'Josefin';
            text-transform: uppercase;
            text-align: center;
            opacity: 0;
            transition: 0.5s;
            transition-delay: 0.25s;
            cursor: pointer;

            &:hover {
                opacity: 1;
                transition-delay: 0s;
            }
        }
    }
}

// keyframes
@-webkit-keyframes slideOutRightNav {
    from {
        -webkit-transform: translate3d(0, 0, 0);
        transform: translate3d(0, 0, 0);
    }

    to {
        -webkit-transform: translate3d(-0.9 * $nav-width, 0, 0);
        transform: translate3d(-0.9 * $nav-width, 0, 0);
    }
}
@keyframes slideOutRightNav {
    from {
        -webkit-transform: translate3d(0, 0, 0);
        transform: translate3d(0, 0, 0);
    }

    to {
        -webkit-transform: translate3d(-0.9 * $nav-width, 0, 0);
        transform: translate3d(-0.9 * $nav-width, 0, 0);
    }
}
.animate__slideOutRightNav {
    -webkit-animation-delay: 0.1s;
    animation-delay: 0.1s;
    -webkit-animation-name: slideOutRightNav;
    animation-name: slideOutRightNav;
}

@-webkit-keyframes slideInRightNav {
    from {
        -webkit-transform: translate3d(-0.9 * $nav-width, 0, 0);
        transform: translate3d(-0.9 * $nav-width, 0, 0);
    }

    to {
        -webkit-transform: translate3d(0, 0, 0);
        transform: translate3d(0, 0, 0);
    }
}
@keyframes slideInRightNav {
    from {
        -webkit-transform: translate3d(-0.9 * $nav-width, 0, 0);
        transform: translate3d(-0.9 * $nav-width, 0, 0);
    }

    to {
        -webkit-transform: translate3d(0, 0, 0);
        transform: translate3d(0, 0, 0);
    }
}
.animate__slideInRightNav {
    -webkit-animation-name: slideInRightNav;
    animation-name: slideInRightNav;
}
